.ContainerMapText{
  display: flex;
}
.district:hover {
  stroke: #33ff19;
  stroke-width: 5
}
.districtActive {
  stroke: #33ff19;
  stroke-width: 5
}
.ContainerTextImage{
  display: flex;
}
.Text{
  height: 50vh;
  min-height: 500px;
  background: #EAE1D3;
  border: 4px solid #B49967;
  box-sizing: border-box;
  padding: 0px 25px;
  margin-bottom: 20px;

  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 300;
  font-size: 1vw;
  color: #000000;
  overflow: auto;
}

@media (max-width: 1600px) {
  .Text {
     font-size: 1.25vw;
  }
}
.Image {
  height: 50vh;
  min-height: 500px;
  object-fit: cover;
  margin-left: 20px;
  max-width: 200px;
}
.ImageVideo {
  display: flex;
  margin: auto;
  object-fit: fill;
  height: 30vh;
  cursor: pointer;
}
.ButtonsPanel {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 30px 0px;
}
.ButtonsPanel > div{
  margin: 0px 10px;
}