.ContainerImageText{
  display: flex;
  flex-direction: row;
}
.Card {
  flex: 2;
  margin-right: 20px;
  max-height: 400px;
  text-align: center;
}
.Image {
  width: 100%;
  max-height: 350px;
  max-width: 240px;
  object-fit: cover;
}
.ImageText {
  margin: 10px auto;
  width: 100%;
  max-width: 240px;

  font-family: Alice;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  
  color: #FFFFFF;
}
.ImageVideo {
  display: flex;
  margin: 20px auto;
  object-fit: fill;
  height: 30vh;
  cursor: pointer;
  
}
.Gallery{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 40px 0px;
}
.Arrow {
  position: relative;
  max-height: 400px;
  width: 80px;
}
.Arrow > button {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
  cursor: pointer;
  background: rgba(0, 0, 0, 0);
  width: 100%;
  padding: 0px;
  border: 0px;
}
.GalleryImages{
  flex: 2;
  width: 25%;
  margin: 0px 10px;
  max-height: 400px;
  object-fit: contain;
  cursor: pointer;
}
.BackButton{
  float: right;
  position: relative;
  width: 250px;
  margin: 20px 0px;
}