.ModalWrapper {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  z-index: 10;
}
.ModalBody {
  text-align: center;
  width: 60%;
  margin: auto;
  margin-top: 5vh;
  
  overflow-y: auto;
  overflow-x: auto;

  z-index: 20;
}
.ModalBody::-webkit-scrollbar {
  height: 0;
}
.ModalClose {
  cursor: pointer;
  position: absolute;
  right: 20%;
  width: 50px;
  margin-top: 10px;
  z-index: 30;
}