.Text{
  margin: 20px 0px;
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  color: #FFFFFF;
}
.Content{
  margin: 20px 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}
.Image {
  margin: 20px;
  border: 4px solid #B49967;
  box-sizing: border-box;
  object-fit: cover;
}

.Arrows {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.Arrow {
  position: relative;
  max-height: 400px;
  width: 80px;
  margin: 30px 0px;
}
.Arrow > button {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
  cursor: pointer;
  background: rgba(0, 0, 0, 0);
  width: 100%;
  padding: 0px;
  border: 0px;
}
.BackButton{
  float: right;
  position: relative;
  width: 250px;
  margin: 20px 0px;
}