.App {
  display: flex;
  flex-direction: column;
  background: rgba(24, 66, 135, 1);
  min-height: 100vh; 
}

::-webkit-scrollbar { 
  width: 6px
}
::-webkit-scrollbar-button {  
  background-color: rgba(217, 202, 176, 1);
}
::-webkit-scrollbar-track {  
  background-color: rgba(217, 202, 176, 1);
}
::-webkit-scrollbar-track-piece { 
  background-color: rgba(217, 202, 176, 1);
}
::-webkit-scrollbar-thumb { 
  height: 50px; 
  background-color: #B49967; 
  border-radius: 3px;
}