.Content{
  margin: 20px 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}
.Button {
  position: relative;
  width: 400px;
  height: 125px;
  background: #B49967;
  border-radius: 5px;
  margin: 10px;
}
.ButtonText {
  width: 100%;
  margin: 0px;
  position: absolute;
  top: 50%;
  transform: translate(0%, -50%);

  font-family: "Alice";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  /* identical to box height */

  text-align: center;

  color: #FFFFFF;
}

.ContainerImageText {
  width: 20%;
  margin: 10px;
}
.Image{
  width: 100%;
  cursor: pointer;
}
.ImageText{
  text-align: center;
  width: 100%;
  padding: 10px;
  color: #FFFFFF;
}
.Arrows {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.Arrow {
  position: relative;
  max-height: 400px;
  width: 80px;
  margin: 30px 0px;
}
.Arrow > button {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
  cursor: pointer;
  background: rgba(0, 0, 0, 0);
  width: 100%;
  padding: 0px;
  border: 0px;
}
.BackButton{
  float: right;
  position: relative;
  width: 250px;
  margin: 20px 0px;
}