.Content{
  margin: 20px 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}
.Card{
  flex-basis: 15vw;
}
.Image {
  border: 4px solid #B49967;
  box-sizing: border-box;
  object-fit: cover;
}
.Text{
  position: relative;
  width: calc(100% - 40px);
  height: 60px;
  margin: 0px auto;
  background: #B49967;
  border-radius: 5px;
  padding: 10px;
  transform: translateY(-60px);
  text-align: center;

  font-family: "Alice";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: center;

  color: #000000;
}
.Text > p {
  width: calc(100% - 20px);
  margin: 0px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.Arrows {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.Arrow {
  position: relative;
  max-height: 400px;
  width: 80px;
  margin: 30px 0px;
}
.Arrow > button {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
  cursor: pointer;
  background: rgba(0, 0, 0, 0);
  width: 100%;
  padding: 0px;
  border: 0px;
}
.BackButton{
  float: right;
  position: relative;
  width: 250px;
  margin: 20px 0px;
}