.Container {
  height: 110px;
  padding: 20px 0px 20px 0px;
  display: flex;
}
.Image {
  object-fit: fill;
}
.TextContainer {
  flex: 1;
  background: rgba(180, 153, 103, 1);
  text-align: center;
}
.Text {
  margin: 20px 0px 21px 0px;
  font-family: "Alice";
  font-style: normal;
  font-size: 3vw;
  font-weight: 400;
  line-height: 69px;
  letter-spacing: 0em;
  text-align: center;
  color: rgba(255, 255, 255, 1);
}
@media (min-width: 1600px) {
  .Text {
     font-size: 50px;
  }
}
@media only screen and (max-width: 700px){
  .responsive {
    width: 49.99999%;
    margin: 6px 0;
  }
}