.Container {
  height: 60px;
  padding: 3px 20px;
  background: #B49967;
  border-radius: 5px;
  cursor: pointer;

  font-family: "Alice";
  font-style: normal;
  font-weight: normal;
  font-size: 1.5vw;
  line-height: 27px;
  text-align: center;
  display: table-cell;
  vertical-align: middle;
  min-width: 200px;
  
  color: #FFFFFF;
}

@media (min-width: 1700px) {
  .Container {
     font-size: 30px;
  }
}