.Container {
  background: #EAE1D3;
  border: 4px solid #B49967;
  box-sizing: border-box;
}
.Text {
  overflow: auto;
  padding: 0px 25px;
  max-height: 100%;
}
.OpenButton{
  cursor: pointer;
  position: relative;
  float: right;
  width: 200px;
  height: 25px;
  margin: 10px 0px 0px 0px;
  font-family: "Alice";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  /* identical to box height */

  text-align: center;
}