.Content{
  border: 4px solid #B49967;
  padding: 10px;
  margin: 20px 0px;
}
.Title{
  text-align: center;
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 46px;
  text-align: center;

  color: #EAE1D3; 
}
.Columns{
  column-count: 3;
  column-gap: 40px;
}
.Record{
  display: flex;
  flex-direction: row;
  margin: 10px;
}
.Record:nth-child(even){
  display: flex;
  flex-direction: row-reverse;
}
.Text {
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  color: #FFFFFF;
}
.Image {
  border-radius: 100%;
}
.ImageText{
  text-align: center;
  
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 24px;
  color: #FFFFFF;
}
.Quotes{
  text-align: right;
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 24px;
  color: #FFFFFF;
}
.Arrows {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.Arrow {
  position: relative;
  max-height: 400px;
  width: 80px;
  margin: 30px 0px;
}
.Arrow > button {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
  cursor: pointer;
  background: rgba(0, 0, 0, 0);
  width: 100%;
  padding: 0px;
  border: 0px;
}
.BackButton{
  float: right;
  position: relative;
  width: 250px;
  margin: 20px 0px;
}